import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { withRouter } from 'react-router';
import $ from 'jquery'

import Main from './main.component';

// styles images and icons
import { Row, Col } from 'react-bootstrap';
import {
    TextField, InputLabel, FormControl, Select,
    Button, FormLabel, RadioGroup, FormControlLabel, Radio
} from '@material-ui/core';

// language for test
import text from '../../languages/english/translate.json';

class Registration extends Component {
    constructor(props) {
        super(props);
        this.state = {
            userType: "Student",
            errors: {
                firstName: '',
                lastName: '',
                password: '',
                confirmPassword: '',

            }
        }
    }
    componentDidMount() {
        console.log(this.props)
        if (this.props.location?.state) {
            let data = this.props.location.state
            console.log(data)
            data.email && data.code &&
            this.setState({ ...this.state, email: data.email, code: data.code })

            console.log(data.Gw)
            if(data.profileObj){
                // hide password inputs
                let inputs = document.querySelectorAll(".password");
                console.log(inputs);
                inputs.forEach(item=>{
                    console.log(item)
                    item.classList.add('d-none')});

                this.setState({ ...this.state, firstName:data.profileObj?.givenName, lastName: data.profileObj?.familyName,
                                accessToken : data.accessToken , provider : data.wc?.idpId})
            }
        }
        
    }
    render() {
        let data = text.registration;
        const validPassword = RegExp(/(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}/);

        const handleChange = props => event => {
            const value = event.target.value;
            this.setState({ ...this.state, [props]: event.target.value });


            let errors = this.state.errors;
            let gender = this.state.gender;
            errors.form = ''
            errors.loginSucc = ''
            switch (props) {
                case 'firstName':
                    errors.firstName =
                        value.length >= 3 && value.length < 191
                            ? ''
                            : text.validation.name;
                    break;
                case 'lastName':
                    errors.lastName =
                        value.length >= 3 && value.length < 191
                            ? ''
                            : text.validation.name;
                    break;
                case 'password':
                    errors.password =
                        validPassword.test(value)
                            ? ''
                            : text.validation.password;
                    break;
                case 'confirmPassword':
                    errors.confirmPassword =
                        validPassword.test(value)
                            ? ''
                            : text.validation.password;
                    break;
                case 'gender':
                    gender =
                        value === "true"
                            ? true
                            : false;
                        this.setState({...this.state , gender : gender})
                    break;
                default:
                    break;
            }
        }
        const validateForm = (errors) => {
            let valid = true;
            Object.values(errors).forEach(
                // if we have an error string set valid to false
                (val) => val.length > 0 && (valid = false)
            );
            return valid;
        }
        const handleUserType = () =>{
            let userType = ''
            // register as student
            if (this.state.userType === 'Student') {
                userType = 'studentsInput'
            }
            // regiter as an instructor
            else {
                userType = 'subInstructorsInput'
            }
            return userType;
        }
        const registerWithEmail = () => {
            let userType = handleUserType;
            console.log(userType)
                $.ajax({
                    url: 'https://quranacademy.black-squares.com/graphql',
                    method: 'post',
                    data: {
                        query: `
                        mutation($${userType} : ${userType}  $email : Email!
                            $pin_code :String!){
                            globalRegister(data: {
                                ${userType} : $${userType} ,
                                verifyPinCodeInput : {
                                email : $email
                                pin_code : $pin_code
                                }
                            }){
                                BearerTokenResponse{
                                access_token
                                refresh_token
                                expires_in
                                }
                                Status{
                                code
                                message
                                check
                                }
                            }
                            }
                            `,
                        variables: {
                            [userType] :
                            {
                                "first_name": this.state.firstName,
                                "last_name": this.state.lastName,
                                "password": this.state.password,
                                "date_of_birth": this.state.dateOfBirth
                            },
                            "pin_code": this.state.code,
                            "email": this.state.email
                        }
                    },
                    dataType: 'json',
                    success: res => {
                        console.log(res)
                    },
                    cache: err => console.log(err)
                });
        }
        const registerWithSocial = () => {
            let userType = handleUserType;
            console.log(userType)
            $.ajax({
                url: 'https://quranacademy.black-squares.com/graphql',
                method: 'post',
                data: {
                    query: `
                    mutation($${userType} : ${userType}  $token : String!
                        $provider :String!){
                        globalRegister(data: {
                            ${userType} : $${userType} ,
                            verifyPinCodeInput : {
                            token : $token
                            provider : $provider
                            }
                        }){
                            BearerTokenResponse{
                            access_token
                            refresh_token
                            expires_in
                            }
                            Status{
                            code
                            message
                            check
                            }
                        }
                        }
                        `,
                    variables: {
                        [userType] :
                        {
                            "first_name": this.state.firstName,
                            "last_name": this.state.lastName,
                            "date_of_birth": this.state.dateOfBirth
                        },
                        "provider": this.state.provider,
                        "token": this.state.accessToken
                    }
                },
                dataType: 'json',
                success: res => {
                    console.log(res)
                },
                cache: err => console.log(err)
            });
        }
        const handleRegister = (event) => {
            event.preventDefault();

            if (validateForm(this.state.errors)) {
                console.log("valid form");
                // regiter with email
                if (this.state.code  && this.state.confirmPassword === this.state.password) {
                    registerWithEmail()
                }
                // register with google 
                else {
                    registerWithSocial()
                }
            }
            else {
                console.log("not valid!")
            }
        }
        console.log(this.state);
        return (
            <Main
                content={
                    <Row className="alig-items-center mb-3">
                        <Col className="ml-auto " md={5}>
                            <div className="shadow bg-white p-4">
                                <form onSubmit={handleRegister}>
                                    <div>
                                        <h4>{data.title}</h4>
                                        <p>{data.description}</p>
                                    </div>
                                    <div>
                                        <div className="mb-3 w-100">
                                            <TextField
                                                required
                                                className="col-6 pr-2"
                                                size="small"
                                                label={data.firstName}
                                                variant="outlined"
                                                value={this.state.firstName}
                                                onChange={handleChange('firstName')}
                                            />
                                            <TextField
                                                required
                                                className="col-6 "
                                                size="small"
                                                label={data.lastName}
                                                variant="outlined"
                                                inputProps ={{
                                                    value : this.state.lastName
                                                }}
                                                onChange={handleChange('lastName')}
                                            />
                                        </div>
                                        <div  className="mb-3 password">
                                            <TextField
                                                fullWidth
                                                type="password"
                                                size="small"
                                                label={data.password}
                                                variant="outlined"
                                                onChange={handleChange('password')}

                                            />
                                        </div>
                                        <div className="mb-3 password">
                                            <TextField
                                                fullWidth
                                                type="password"
                                                size="small"
                                                label={data.confirmPassword}
                                                variant="outlined"
                                                onChange={handleChange('confirmPassword')}
                                            />
                                        </div>
                                        <div className="mb-3">
                                            <InputLabel className="mb-1" htmlFor="component-outlined">{data.dateOfBirth}</InputLabel>
                                            <TextField
                                                fullWidth
                                                size="small"
                                                type="date"
                                                variant="outlined"
                                                onChange={handleChange('dateOfBirth')}
                                            />
                                        </div>
                                    </div>
                                    <FormControl
                                        className="mb-3"
                                        variant="outlined" fullWidth size="small">
                                        <InputLabel
                                            fullWidth
                                            htmlFor="outlined-age-native-simple">
                                            Age
                                    </InputLabel>
                                        <Select
                                            native
                                            name={data.gender}
                                            onChange={handleChange('gender')}
                                            label={data.gender}
                                            inputProps={{
                                                name: data.gender,
                                                id: 'outlined-age-native-simple',
                                            }}
                                        >   
                                            <option value=""></option>
                                            <option value={true}>Male</option>
                                            <option value={false}>Female</option>
                                        </Select>
                                    </FormControl>
                                    <FormControl component="fieldset">
                                        <FormLabel className="mb-1" component="legend">{data.Register.title} :</FormLabel>
                                        <RadioGroup
                                            onChange={handleChange('userType')}
                                            defaultValue={this.state.userType}
                                            row
                                            aria-label="userType"
                                            name="userType">
                                            <FormControlLabel
                                                value={data.Register.Student}
                                                control={<Radio color="primary" />}
                                                label={data.Register.Student}
                                                labelPlacement="start"
                                            />
                                            <FormControlLabel
                                                value={data.Register.instructor}
                                                control={<Radio color="primary" />}
                                                label={data.Register.instructor}
                                                labelPlacement="start"
                                            />
                                        </RadioGroup>
                                    </FormControl>
                                    <div className="m-0 align-items-center">
                                        <Button
                                            type="submit"
                                            fullWidth
                                            className="btn-green"
                                        >
                                            {data.registerBtn}
                                        </Button>
                                    </div>
                                    <div className="text-center">
                                        {data.haveAcc} <Link className="text-green" to="/login">{data.LoginBtn}</Link>
                                    </div>
                                </form>
                            </div>
                        </Col>
                    </Row>
                } />
        )
    }
}

export default withRouter(Registration)

