import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { withRouter } from 'react-router';
import $ from 'jquery';

import Main from './main.component';

// styles images and icons
import { Row, Col } from 'react-bootstrap';
import { TextField, Button } from '@material-ui/core';
import mail from '../../images/login/icon.svg';

// language for test
import text from '../../languages/english/translate.json';



class Reset extends Component {
    constructor(props) {
        super(props);
        this.state = {
            errors: {
                password: '',
                confirmPassword: ''
            }
        }
    }
    componentDidMount() {
        console.log(this.props);
        if (this.props.location?.state) {
            let data = this.props.location.state;
            this.setState({ ...this.state, email: data.email, code: data.code })
        }
    }
    render() {
        let data = text.passwordReset;
        const validPassword = RegExp(/(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}/);

        const handleChange = props => event => {
            const value = event.target.value;

            this.setState({ ...this.state, [props]: event.target.value });

            let errors = this.state.errors;
            errors.form = ''
            errors.sentSucc = ''
            switch (props) {
                case 'password':
                    errors.password =
                        validPassword.test(value)
                            ? ''
                            : text.validation.password;
                    break;
                case 'confirmPassword':
                    errors.confirmPassword =
                        validPassword.test(value)
                            ? ''
                            : text.validation.password;
                    break;
                default:
                    break;
            }
        }
        const validateForm = (errors) => {
            let valid = true;
            Object.values(errors).forEach(
                // if we have an error string set valid to false
                (val) => val.length > 0 && (valid = false)
            );
            return valid;
        }
        const handleReset = (event) => {
            event.preventDefault();
            if (validateForm && this.state.confirmPassword === this.state.password) {
                console.log("yala")
                $.ajax({
                    url: 'https://quranacademy.black-squares.com/graphql',
                    method: 'post',
                    data: {
                        query: ` mutation( $password : Password! $email : Email! $pin_code : String !){
                            globalPasswordresets(data: {
                              password  : $password
                              verifyPinCodeInput : {
                                email : $email ,
                                pin_code : $pin_code
                              }
                            }){
                              BearerTokenResponse{
                                access_token
                                refresh_token
                                expires_in
                                token_type
                              }
                              Student{
                                email
                              }
                              Instructor{
                                email
                              }
                              SubInstructor{
                                email
                              }
                              Status{
                                code
                                message
                              }
                            }
                          }`,
                        variables: {
                            email: this.state.email,
                            pin_code: this.state.codeFor,
                            password: this.state.password
                        }
                    },
                    dataType: 'json',
                    success: res => {
                        console.log(res)
                        if (res.data) {
                            console.log(res.data)
                            if (res.data.globalPasswordresets.Status.code === 200) {
                                this.props.history.push({
                                    pathname : "/login"
                                });
                            } else {
                                console.log(res)
                                let errors = this.state.errors;
                                errors.formSucc = res.data.globalSendPinCode.Status.message
                                this.setState({ ...this.state, errors: errors })
                            }
                        }
                    },
                    cache: err => {
                        console.log(err)
                        // let errors = this.state.errors;
                        // errors.form = res.data.globalSendPinCode.Status.message
                        // this.setState({ ...this.state, errors: errors })
                    }
                });
            }
            else {
                let errors = this.state.errors;
                errors.form = text.validation.confirmPassword
                this.setState({ ...this.state, errors: errors })
            }
        }
        console.log(this.state);
        return (
            <Main
                content={
                    <Row className="alig-items-center justify-content-center mt-5  mb-5 ">
                        <Col md={4} className="bg-white  text-center shadow pt-5 pb-5 pr-4 pl-4">
                            <form onSubmit={handleReset}>
                                <div className="mb-2">
                                    <img src={mail} alt="mail" className="img-flid" />
                                </div>
                                <div className="mb-3">
                                    <h4>{data.title}</h4>
                                    <p>{data.description}</p>
                                </div>
                                <div className="mb-3">
                                    <TextField
                                        size="small"
                                        type="password"
                                        fullWidth
                                        required
                                        onChange={handleChange('password')}
                                        label={data.password}
                                        variant="outlined"
                                    />
                                    <div className="text-danger text-left"><p className="mb-1">{this.state.errors.password}</p></div>
                                </div>
                                <div className="mb-1">
                                    <TextField
                                        size="small"
                                        type="password"
                                        fullWidth
                                        required
                                        onChange={handleChange('confirmPassword')}
                                        label={data.confirmPassword}
                                        variant="outlined"
                                    />
                                    <div className="text-danger text-left"><p className="mb-1">{this.state.errors.confirmPassword}</p></div>
                                </div>
                                <div className="text-danger text-left"><p className="mb-1">{this.state.errors.form}</p></div>
                                <div className="mt-3">
                                    <Button
                                        fullWidth
                                        type="submit"
                                        className="btn-green">
                                        {data.resetBtn}
                                    </Button>
                                </div>
                            </form>
                        </Col>
                    </Row>
                } />
        )
    }
}

export default withRouter(Reset)