import React from 'react';

// styles and images
import { Navbar, Container, Nav } from 'react-bootstrap';
import logoWeb from '../../images/logo/logo-1.svg'
import logoMob from '../../images/logo/logo.svg'

// language for test
import text from '../../languages/english/translate.json';

export default function Header() {
    let data = text?.home.header;
    return (
        <Navbar collapseOnSelect fixed="top" expand="lg" bg="white" className="shadow" variant="light">
                {/* for web */}
                <Navbar.Brand className="d-web" href="#intro">
                    <img src={logoWeb} className="img-fluid" alt="logo" />
                </Navbar.Brand>

                {/*  for mob */}
                <Navbar.Brand className="d-mob  mr-0" href="#intro">
                    <img src={logoMob} className="w-50" alt="logo" />
                </Navbar.Brand>
                <Nav className="d-mob">
                    <button className="btn-green-outline  mr-1 ">Register</button>
                    <button className="btn-green">Sign In</button>
                </Nav>

                {/*  5s mob or similer*/}
                <Navbar.Brand className="d-sm-mob mr-0" href="#intro">
                    <img src={logoMob} className="w-50" alt="logo" />
                </Navbar.Brand>
                <Nav className="d-sm-mob">
                    <button className="btn-green-outline  mr-1 pr-1 pl-1">Register</button>
                    <button className="btn-green pr-1 pl-1">Sign In</button>
                </Nav>

                <Navbar.Toggle aria-controls="responsive-navbar-nav" />

                <Navbar.Collapse id="responsive-navbar-nav">
                    <Nav className="m-auto">
                        <Nav.Link
                            className="mr-2 ml-2"
                            href={data.home.url}
                        >
                            {data.home.text}
                        </Nav.Link>
                        <Nav.Link
                            className="mr-2 ml-2"
                            href={data.about.url}
                        >
                            {data.about.text}
                        </Nav.Link>
                        <Nav.Link
                            className="mr-2 ml-2"
                            href={data.services.url}
                        >
                            {data.services.text}
                        </Nav.Link>
                        <Nav.Link
                            className="mr-2 ml-2"
                            href={data.difference.url}
                        >
                            {data.difference.text}
                        </Nav.Link>
                        <Nav.Link
                            className="mr-2 ml-2"
                            href={data.contactUs.url}
                        >
                            {data.contactUs.text}
                        </Nav.Link>
                    </Nav>
                </Navbar.Collapse>
                {/* buttons for web */}
                <Nav className="inline-block d-web">
                    <button className="btn-green-outline mr-2 ml-2">Register</button>
                    <button className="btn-green mr-2 ml-2">Sign In</button>
                </Nav>
        </Navbar>
    )
}